import React from "react";
import styled from "styled-components";
import { Menu } from "@styled-icons/material-sharp/Menu";

const StyledNavigationDrawerButton = styled.button`
  width: 48px;
  height: 48px;

  border-radius: 50%;

  background-color: ${(props) => props.theme.color.ultramarineBlue};
`;

const StyledMenuIcon = styled(Menu)`
  color: ${(props) => props.theme.white};
`;

export default function NavigationDrawerButton(): JSX.Element {
  return (
    <StyledNavigationDrawerButton>
      <StyledMenuIcon />
    </StyledNavigationDrawerButton>
  );
}
